    background: linear-gradient(-135deg, #83cfda, #4fe6d9);
}

.edit_button:active span::after {
    position: absolute;
    top: 0%;
    left: 0%;
    width: 300px;
    height: 90px;
    border-radius: 40px;
    margin-top: -10px;
    margin-left: -27px;
    content: "";
    background: linear-gradient(-135deg, #9acdf7, #6aeeff);
    border-radius: 100%;
    opacity: 0;
    animation: ripple 0.3s 1 ease-out;
}

